* {
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "Avenir";
  font-weight: 300;
  font-style: normal;
  src: url("./fonts/Avenir-Book.ttf") format("truetype");
}
@font-face {
  font-family: "Avenir";
  font-weight: 600;
  font-style: normal;
  src: url("./fonts/Avenir-Heavy.ttf") format("truetype");
}

body {
  font-family: "Avenir", sans-serif;
  font-weight: 300;
}

.search {
  width: 100%;
  position: relative;
  margin: 20px 0;
  outline: none;
}

.input {
  color: #3d4e61;
  background: #fff;
  width: 100%;
  border-radius: 4px;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
}

.large__input {
  font-family: "Avenir", sans-serif;
  font-style: normal;
  height: 70px;
  font-size: 16px;
  line-height: 22px;
  padding-left: 62px;
}

.large__input::placeholder {
  font-weight: 300;
  color: inherit;
}

.large__svg {
  position: absolute;
  height: 19px;
  width: 19px;
  top: 25px;
  left: 26px;
}

.small__input {
  font-family: "Avenir", sans-serif;
  font-style: normal;
  height: 42px;
  font-size: 14px;
  line-height: 19px;
  padding-left: 33px;
}

.small__input::placeholder {
  font-weight: 400;
  color: inherit;
}

.small__svg {
  position: absolute;
  height: 11px;
  width: 11px;
  top: 15px;
  left: 13px;
}

.search:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}

.input:focus {
  font-weight: 600;
}
